                                <el-checkbox v-for="item in notifyGroup" :key="item.id" :label="item.id">{{item.name}}</el-checkbox>
                            </el-checkbox-group>
                        </div>
                    </div>
                </div>
            </bk-tabpanel>
            <!--<bk-tabpanel name="local-draft" :title="i18n.draft">
