                        <draggable
                            v-model="draftArray"
                            :options="{handle:'.col-drag'}">
                                <li
                                    v-for="(draft, index) in draftArray"
                                    :key="draft.key"
                                    :class="{
                                        'clearfix': true,
                                        'draft-item': true
                                    }"
                                    @click="onReplaceTemplate(draft.data.template)">
                                    <div class="draft-content">
                                        <span class="col-item col-drag">
                                        </span>
                                         <span class="col-item col-number">
                                            {{index + 1}}
                                        </span>
                                        <span class="col-item col-message">
                                            {{draft.data.description.message}}
                                        </span>
                                        <span class="col-item col-time">
                                            {{draft.data.description.time}}
                                        </span>
                                        <span class="col-item col-delete" @click.stop="onDeleteDraft(draft.key)">
                                            <i class="common-icon-close"></i>
                                        </span>
                                    </div>
                                </li>
                        </draggable>
                    </ul>
                </div>
            </bk-tabpanel>-->
        </bk-tab>
        <bk-dialog
            :is-show.sync="deleteConfirmDialogShow"
            :quick-close="false"
            :ext-cls="'common-dialog'"
            :title="i18n.tips"
            width="400"
            padding="30px"
            @confirm="onConfirm"
            @cancel="onCancel">
            <div slot="content">{{ i18n.confirm }}</div>
        </bk-dialog>
        <bk-dialog
            :is-show.sync="replaceConfirmDialogShow"
            :quick-close="false"
            :ext-cls="'common-dialog'"
            :title="i18n.replaceTips"
            width="400"
            padding="30px"
            @confirm="onReplaceConfirm"
            @cancel="onReplaceCancel">
            <div slot="content">{{ i18n.replaceConfirm }}</div>
        </bk-dialog>
        <bk-dialog
            :is-show.sync="newConfirmDialogShow"
            :quick-close="false"
            :ext-cls="'common-dialog'"
            :title="i18n.newDraft"
            width="400"
            padding="30px"
            @confirm="onNewConfirm"
            @cancel="onNewCancel">
            <div slot="content">
                <div class="common-form-item" id="draft-dialog-content">
                    <label class="required">{{i18n.draftName}}</label>
                    <div class="common-form-content">
                        <BaseInput v-model="newDraftName" @input="checkDraftName"/>
                        <span v-show="draftIsError" class="common-error-tip error-msg">{{newDraftErrorMessage}}</span>
                    </div>
                </div>
            </div>
        </bk-dialog>
    </div>
</template>
<script>
import '@/utils/i18n.js'
import { mapState, mapMutations } from 'vuex'
import tools from '@/utils/tools.js'
import draggable from 'vuedraggable'
import BaseCheckbox from '@/components/common/base/BaseCheckbox.vue'
import BaseInput from '@/components/common/base/BaseInput.vue'
import VariableEdit from './VariableEdit.vue'
import draft from '@/utils/draft.js'
export default {
    name: 'GlobalVariables',
    components: {
        BaseInput,
        BaseCheckbox,
        VariableEdit,
        draggable
    },
    props: ['businessInfoLoading', 'isTemplateConfigValid', 'isSettingPanelShow', 'draftArray'],
    data () {
        return {
            i18n: {
                global_varibles: gettext('全局变量'),
                new: gettext('新建变量'),
                source: gettext('来源：'),
                two_types: gettext('分两种，输入类型'),
                outputs_type: gettext('或输出类型'),
                desc: gettext('。输入类型变量来自用户添加的变量或者原子/子流程节点输入参数引用的变量；输出类型变量来自原子/子流程节点输出参数引用的变量。'),
                show: gettext('显示：'),
                show_desc: gettext('表示该变量在创建任务填写参数时是否展示给用户，输出类型的变量一定是隐藏的。'),
                outputs: gettext('输出：'),
                outputs_desc: gettext('表示该变量会作为该流程模板的输出参数，在被其他流程模板当做子流程节点时可以引用。'),
                name: gettext('名称'),
                source2: gettext('来源'),
                hide: gettext('隐藏'),
                show2: gettext('显示'),
                // 区别于其他地方的删除翻译，这里为了简写英文翻译，所以是'变量删除'
                delete: gettext('变量删除'),
                inputs: gettext('输入'),
                outputs2: gettext('输出'),
                varHook: gettext('勾选变量作为流程输出参数'),
                varCancelHook: gettext('取消勾选'),
                common: gettext('基础属性'),
                type: gettext('分类'),
                notify_type: gettext('通知方式'),
                timeout: gettext('超时时长'),
                receiver_group: gettext('通知分组'),
                tips: gettext('删除变量'),
                confirm: gettext('确认删除该变量？'),
                categoryTip: gettext('必填项'),
                copied: gettext('已复制'),
                draft: gettext('草稿箱'),
                newDraft: gettext('新建草稿'),
                draftMessage: gettext('草稿名称'),
                replace: gettext('替换'),
                saveTime: gettext('保存时间'),
                replaceTips: gettext('替换流程'),
                replaceConfirm: gettext('是否使用草稿替换当前流程？'),
                serialNumber: gettext('序号'),
                draftName: gettext('草稿名称'),
                sketch: gettext('简述：'),
                draftSketch: gettext('草稿可以用于记录当前流程所有信息，包括流程的节点编排、全局变量、名称、基础属性等信息。草稿箱支持每个流程最多保存50个最近的草稿，该数据存储至本地浏览器的 localStorage 中，每个用户只能查看和编辑自己的草稿。'),
                produce: gettext('生成方式：'),
                draftProduce: gettext('分四种：自动保存，用户自动编排，用户新建草稿，用户切换草稿。'),
                draftProduce1: gettext('自动保存：用户在编辑流程时，每5分钟会自动保存一次。'),
                draftProduce2: gettext('用户自动编排：用户使用画布"自动编排"功能时，会自动把编排前的流程信息保存成草稿。'),
                draftProduce3: gettext('用户新建草稿：用户可以随时把编辑中的流程保存成草稿，并自定义名称。'),
                draftProduce4: gettext('用户切换草稿：用户使用草稿还原数据时，会把还原前的流程信息保存成草稿。'),
                deleteMethod: gettext('删除：'),
                draftDelete: gettext('分两种：单个删除，全部删除。'),
                draftDelete1: gettext('单个删除：用户在编辑流程时，可以在草稿箱中选择某个草稿点击删除按钮进行删除。'),
                draftDelete2: gettext('全部删除：用户可以通过清除浏览器 localStorage 数据来删除所有流程的所有草稿，该操作不可复原，需慎重。')
            },
            showPanel: true,
            isVariableEditing: false,
            theKeyOfEditing: '',
            constantsArray: [],
            selectedTaskCategory: '',
            activeTab: 'global-variable-tab',
            deleteVarKey: '',
            deleteVarIndex: '',
            deleteConfirmDialogShow: false,
            copyText: '',
            replaceConfirmDialogShow: false,
            replaceData: null,
            newConfirmDialogShow: false,
            newDraftName: '',
            draftIsError: false,
            newDraftErrorMessage: ''
        }
    },
    computed: {
        ...mapState({
            'businessBaseInfo': state => state.template.businessBaseInfo,
            'outputs': state => state.template.outputs,
            'constants': state => state.template.constants,
            'timeout': state => state.template.time_out
        }),
        variableData () {
            if (this.theKeyOfEditing) {
                return this.constants[this.theKeyOfEditing]
            } else {
                return {
                    custom_type: 'input',
                    desc: '',
                    key: '',
                    name: '',
                    show_type: 'show',
                    source_info: {},
                    source_tag: '',
                    source_type: 'custom',
                    validation: '^.+$',
                    validator: [],
                    value: ''
                }
            }
        },
        notifyGroup () {
            if (this.businessBaseInfo.notify_group) {
                return this.businessBaseInfo.notify_group.map(item => {
                    return {
                        id: item.value,
                        name: item.text
                    }
                })
            }
            return []
        },
        notifyTypeList () {
            if (this.businessBaseInfo.notify_type_list) {
                return this.businessBaseInfo.notify_type_list.map(item => {
                    return {
                        id: item.value,
                        name: item.name
                    }
                })
            }
            return []
        },
        taskCategories () {
            if (this.businessBaseInfo.task_categories) {
                return this.businessBaseInfo.task_categories.map(item => {
                    return {
                        id: item.value,
                        name: item.name
                    }
                })
            }
            return []
        },
        receiverGroup: {
            get () {
                return this.$store.state.template.notify_receivers.receiver_group
            },
            set (value) {
                this.setReceiversGroup(value)
            }
        },
        notifyType: {
            get () {
                return this.$store.state.template.notify_type
            },
            set (value) {
                this.setNotifyType(value)
            }
        },
        category: {
            get () {
                return this.$store.state.template.category
            },
            set (value) {
                this.setCategory(value)
                this.$emit('onSelectCategory', value)
            }
        }
    },
    watch: {
        constants: {
            handler () {
                this.theKeyOfEditing = ''
                this.isVariableEditing = false
                this.constantsArray = this.getConstantsArray()
            },
            deep: true
        },
        isTemplateConfigValid (val) {
            if (!val) {
                this.activeTab = 'template-config-tab'
            }
        },
        isSettingPanelShow (val) {
            this.showPanel = val
        }
    },
    created () {
        this.constantsArray = this.getConstantsArray()
    },
    methods: {
        ...mapMutations ('template/', [
            'editVariable',
            'deleteVariable',
            'setOutputs',
            'setReceiversGroup',
            'setNotifyType',
            'setOvertime',
            'setCategory'
        ]),
        getConstantsArray () {
            const arrayList = []
            for (let cKey in this.constants) {
                const constant = tools.deepClone(this.constants[cKey])
                arrayList.push(constant)
            }
            const sortedList = arrayList.sort((a, b) => a.index - b.index)
            return sortedList
        },
        togglePanel () {
            this.$emit('toggleSettingPanel', !this.showPanel)
        },
        updateVariableIndex (start, end) {
            const indexChangedVariable = this.constantsArray.slice(start, end)
            indexChangedVariable.forEach((item, index) => {
                item.index = index
                this.editVariable({key: item.key, variable: item})
            })
        },
        onTabChange (tabName) {
            this.activeTab = tabName
        },
        /**
         * 新增变量
         */
        onAddVariable () {
            this.isVariableEditing = true
            this.theKeyOfEditing = ''
            this.$emit('varibleDataChanged')
        },
        /**
         * 编辑变量
         */
        onEditVariable (key) {
            this.isVariableEditing = true
            this.theKeyOfEditing = key
            this.$emit('varibleDataChanged')
        },
        /**
         * 删除变量
         */
        onDeleteVariable (key, index) {
            this.deleteVarKey = key
            this.deleteVarIndex = index
            this.deleteConfirmDialogShow = true
        },
        onChangeEdit (val) {
            this.isVariableEditing = val
        },
        /**
         * 变量输出勾选
         */
        onChangeVariableOutput (key ,checked) {
            const changeType = checked ? 'add' : 'delete'
            this.setOutputs({changeType, key})
            this.$emit('varibleDataChanged')
        },
        /**
         * 变量顺序拖拽
         */
        onDragEnd (event) {
            const { newIndex, oldIndex } = event
            const start = Math.min(newIndex, oldIndex)
            const end = Math.max(newIndex, oldIndex) + 1
            const indexChangedVariable = this.constantsArray.slice(start, end)
            indexChangedVariable.forEach((item, index) => {
                item.index = index + start
                this.editVariable({key: item.key, variable: item})
            })
            this.$emit('varibleDataChanged')
        },
        onChangeTimeout (val) {
            this.setOvertime(val)
        },
        onChangeTaskCategories (id) {
            this.selectedTaskCategory = id
        },
        /**
         * 变量删除弹窗确认
         */
        onConfirm () {
            const key = this.deleteVarKey
            const index = this.deleteVarIndex
            this.$emit('onDeleteConstant', key)
            const len = this.constantsArray.length
            if (len > 1) {
                const indexChangedVariable = this.constantsArray.slice(index + 1, len)
                indexChangedVariable.forEach((item, index) => {
                    item.index -= 1
                    this.editVariable({key: item.key, variable: item})
                })
            }
            this.deleteVariable(key)
            this.$emit('varibleDataChanged')
            this.deleteConfirmDialogShow = false
        },
        onCancel () {
            this.deleteConfirmDialogShow = false
        },
        // 流程替换
        onReplaceTemplate (templateData) {
            this.replaceConfirmDialogShow = true
            this.replaceData = templateData
        },
        // 删除草稿
        onDeleteDraft (key) {
            this.$emit('onDeleteDraft', key)
        },
        // 替换流程
        onReplaceConfirm () {
            this.$emit('onReplaceTemplate', this.replaceData)
            this.replaceConfirmDialogShow = false
        },
        // 取消替换流程
        onReplaceCancel () {
            this.replaceConfirmDialogShow = false
        },
        // 新增草稿
        onNewDraftTemplate () {
            this.newConfirmDialogShow = true
        },
        onNewConfirm () {
            if (!this.draftIsError) {
                // 新增草稿
                this.draftIsError = false
                this.$emit('onNewDraft', this.newDraftName)
                this.newDraftName = ''
                this.newConfirmDialogShow = false
            }
        },
        onNewCancel () {
            this.newDraftName = ''
            this.newConfirmDialogShow = false
        },
        checkDraftName () {
            if (this.newDraftName.length > 10) {
                this.draftIsError = true
                this.newDraftErrorMessage = gettext('草稿名称不能超过10个字符')
            }
            else if (this.newDraftName.length === 0) {
                this.draftIsError = true
                this.newDraftErrorMessage = gettext('草稿名称不能为空')
            }
            else {
                this.draftIsError = false
            }
        }
    }
}
</script>
<style lang="scss" scoped>
