    background: $whiteDefault;
    border-left: 1px solid $commonBorderColor;
    // box-shadow: -1px 0 4px #dfdfdf;
    z-index: 4;
    transition: right 0.5s ease-in-out;
    &.actived {
        right: 0;
    }
}
.toggle-button {
    position: absolute;
    top: 0;
    left: -20px;
    width: 20px;
    height: 50px;
    line-height: 50px;
    color: $whiteDefault;
    background: $blueThinBg;
    text-align: center;
    cursor: pointer;
    &:hover {
