<template>
    <div class="edit-dialog-wrapper">
        <bk-dialog
            :quick-close="false"
            :ext-cls="'common-dialog'"
            :title="i18n.edit"
            width="600"
            :is-show.sync="isShow"
            @confirm="onConfirm"
            @cancel="onCancel">
            <div slot="content">
                <RenderForm
                    ref="renderForm"
                    :scheme="renderConfig"
                    :formOption="renderOption"
                    v-model="formData">
                </RenderForm>
            </div>
        </bk-dialog>
    </div>
</template>
<script>
import '@/utils/i18n.js'
import tools from '@/utils/tools.js'
import RenderForm from '@/components/common/RenderFormNew/RenderForm.vue'
export default {
    name: 'VariableEditDialog',
    components: {
        RenderForm
    },
    props: ['isShow', 'renderConfig', 'renderData', 'renderOption'],
    data () {
        return {
            i18n: {
                edit: gettext("编辑变量")
            },
            formData: tools.deepClone(this.renderData)
        }
    },
    methods: {
        onConfirm () {
            let formValid = true
            if (this.$refs.renderForm) {
                formValid = this.$refs.renderForm.validate()
            }
            if (!formValid) return
            this.$emit('onConfirmDialogEdit', this.formData)
        },
        onCancel () {
            this.$emit('onCancelDialogEdit')
        }
    }
}
</script>
<style lang="scss" scoped>
.edit-dialog-wrapper {
    /deep/ .tag-form {
        margin-left: 0;
    }
    /deep/ .bk-dialog-body {
        max-height: 500px;
        overflow-y: auto;
    }
}
</style>


